<template>
	<view class="box">
		<view class="cu-modal" :class="modalName=='Image'?'show':''" @tap="hideModal" v-if="itempic&&qrPath">
			<view class="cu-dialog" @tap="hideModal">
				<view class="bg-img">
					<wm-posters @success="SuccessImage" :imgSrc="itempic" :QrSrc="qrPath" :Title="itemtitle" :PriceTxt="itemendprice"
					 :OriginalTxt="itemprice" :LineType='false'></wm-posters>
				</view>
			</view>
		</view>
		<tki-qrcode ref="qrcode" :val="erweima" :size="600" background="#fff" foreground="#000" pdground="#000" :onval="true"
		 :loadMake="true" @result="qrR" :show="false"></tki-qrcode>
		<view class="bg-btn">
			保存海报
		</view>

		
	</view>
</template>
<script>
	import wmPosters from '@/components/wm-poster/wm-posters.vue';
	import tkiQrcode from '@/components/tki-qrcode/tki-qrcode.vue';
	export default {
		data() {
			return {
				webviewStyles: {
					progress: {
						color: '#FF3333'
					}
				},
				modalName: '',
				logo: '../../static/img/maos.png',
				TabCur: 0,
				TabCurs: 0,
				isEnable: '否',
				erweima: "../../static/img/erweima.jpg",
				qrPath: "",
				scrollLeft: 0,
				scrollLefts: 0,
				couponlist: [],
				selectId: 1,
				page: 1,
				min_id: 1,
				cid: 0,
				type: 4,
				sort: 3,
				genderKey: 'gender',
				loadingType: 0,
				isInvitation: 0,
				scrollTop: false,
				contentText: {
					contentdown: '上拉显示更多',
					contentrefresh: '正在加载...',
					contentnomore: '没有更多数据了'
				},
				itemendprice: '识别二维码免费领取',
				itemtitle: '',
				itemprice: '',
				itempic: ''
			};
		},
		components: {
			tkiQrcode,
			wmPosters
		},
		onLoad() {
		this.clicklItem()
		},
		methods: {
			clicklItem() {
				this.itemprice = '50';
				this.itempic = require('@/static/images/apply-agent-img.png');
				this.itemtitle = '';
				uni.showLoading({
					title: '海报生成中...'
				});
			},
			qrR(path) {
				this.qrPath = path;
			},
			hideModal() {
				this.modalName = null
			},
			SuccessImage(qrR) {
				this.modalName = "Image"
				uni.hideLoading()
			},
		}
	}
</script>

<style lang="scss">
	.box {
		width: 100%;

	}

	.poster {
		width: 100%;
		height: auto;
	}

	.bg-btn {
		width: 80%;
		margin: 0 auto;
		margin-top: 30rpx;
	}
</style>
